.main{
    display: -webkit-flex;
    display: flex;
}

.title{
    color: #BF00FF;
    padding: 10px;
    text-decoration: underline; 
    font-size: 40px; 
    line-height: 50px;
    border-radius: 4px;  
    border-color: black;   
    background: #E6E6E6;
    position: relative;  
    text-align: -webkit-center; 
    display: block;
}

.title_garzon {
    position: relative;
    font-size: 40px;
